<template>
  <div class="dowebok">
    <div class="logo"><h1>系统登录</h1></div>
    <form action="login" accept-charset="utf-8" id="login_form" class="loginForm">

      <div class="form-item">
        <input  type="text"  placeholder="账号" v-model="username">
        <!--  -->
      </div>
      <div class="form-item">
        <input  type="password"  placeholder="登录密码" v-model="password">
        <!--   -->
      </div>
      <div class="form-item"><button id="submit" @click.prevent="login()">登 录</button></div>
      <div class="reg-bar">
        <a class="reg" href="javascript:">立即注册</a>
        <a class="forget" href="javascript:">忘记密码</a>
      </div>
    </form>
  </div>
</template>

<script>
import axios from 'axios'
import qs from 'qs'

export default {
  name: 'Login',
  data () {
    return {
      username: '',
      password: ''
    }
  },
  methods: {
    login () {
      axios.post('http://localhost:8888/login', qs.stringify(
        {
          username: this.username,
          password: this.password
        }
      ))
        .then((response) =>{
          console.log(response)
          //this.$eventBus.$emit("username",this.username)
          if(response.data !== "") {
            this.$router.push({
              path: '/menu', query: {
                username: response.data.userName
              }
            })
          }
          else{
            alert("用户名密码错误！")
            this.username=''
            this.password=''
          }
        })
      //this.$router.push('/menu')
    }
  }
}
</script>

<style scoped>
@import './css/login_bg.css';
</style>
