import Vue from 'vue'
import VueRouter from 'vue-router'
import Login from '../views/Login'
import Menu from '../views/Menu'

Vue.use(VueRouter)

const routes = [
    {
        path: '/',
        name: 'login',
        component: Login
    },
    {
        path: '/menu',
        name: 'menu',
        component: Menu,
        children: [
            {
                path: '/addUser',
                name: 'addUser',
                component: () => import(/* webpackChunkName: "addUser" */ '../views/user/AddUser.vue')
            },
            {
                path: '/showUser',
                name: 'showUser',
                component: () => import(/* webpackChunkName: "addUser" */ '../views/user/ShowUser.vue')
            },
            {
                path: '/addDataSource',
                name: 'addDataSource',
                component: () => import(/* webpackChunkName: "addDataSource" */ '../views/data_source/AddDataSource.vue')
            },
            {
                path: '/showDataSource',
                name: 'showDataSource',
                component: () => import(/* webpackChunkName: "mergeData" */ '../views/data_source/ShowDataSource.vue')
            },
            {
                path: '/showData',
                name: 'showData',
                component: () => import(/* webpackChunkName: "mergeData" */ '../views/data/ShowData.vue')
            },
            {
                path: '/mergeData',
                name: 'mergeData',
                component: () => import(/* webpackChunkName: "mergeData" */ '../views/data/MergeData.vue')
            }
        ]
    }
]
// 解决ElementUI导航栏中的vue-router在3.0版本以上重复点菜单报错问题
const originalPush = VueRouter.prototype.push
VueRouter.prototype.push = function push(location) {
    return originalPush.call(this, location).catch(err => err)
}

const router = new VueRouter({
    routes
})

export default router
